
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'


export default {
data() {
return {
showMore: false,
data: [],
deleteStatus: true,
familyName: '',
deviceName: '',
pattern: '',
conditionData: [],
familyImg: 'http://www.xiaoxiangba.com/assets/img/bg01.jpg'
}
},
created() {
this.clickInit()
},
watch:{
data(val){
console.log(val.length);
if(val.length === 0){
this.deleteStatus = false
}
}
},
methods: {
addCondition() {
this.$router.push('/scene/created_condition?id=' + this.$route.query.id)
},
clickInit() {
this.$axios.get(`/SmartHome/get_scenebyid?id=${this.$route.query.id}`)
.then((res) => {
this.data = res.data[0]
this.familyName = res.data[0].family.familyName
this.deviceName = res.data[0].device.name
this.pattern = res.data[0].scene.pattern
this.conditionData = res.data[0].condition
})
},
clickDetele(sceneId) {
var userId = JSON.parse(localStorage.mydata).user.id
this.$axios.post('/SmartHome/del_scene', {
sceneId: this.$route.query.id
})
.then((res) => {
console.log(res)
this.data = []
})
},
clickMore() {
this.showMore = !this.showMore
},
createFamily() {
this.$router.push('/my_family/create_family')
},
joinFamily() {
this.$router.push('/my_family/join_family')
}
},
components: {
XHeader,
Group,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton,
}
}



